import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

const routes: Routes = [
  { path: 'worldTime', loadChildren: () => import('./world-time/world-time.module').then(m => m.WorldTimeModule) },
  { path: 'countDown', loadChildren: () => import('./countdown/countdown.module').then(m => m.CountdownModule) },
  { path: 'thaiCalendar', loadChildren: () => import('./thai-calendar/thai-calendar-routing.module').then(m => m.routes) }
  ];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WorldTimeRoutingModule { }
