import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { WorldTimeRoutingModule } from './time-routing.module';
import { WorldTimeComponent } from './time.component';
import { ThaiCalendarComponent } from './thai-calendar/thai-calendar.component';


@NgModule({
  declarations: [WorldTimeComponent],
  imports: [
    CommonModule,
    WorldTimeRoutingModule,
    ThaiCalendarComponent,
  ]
})
export class WorldTimeModule { }
