import { Component } from '@angular/core';

import {MatIconModule} from '@angular/material/icon';
import {MatDatepickerInputEvent, MatDatepickerModule} from '@angular/material/datepicker';
import {MatInputModule} from '@angular/material/input';
import {MatFormFieldModule} from '@angular/material/form-field';
import {MatNativeDateModule} from '@angular/material/core';
import { CommonModule } from '@angular/common';  


import {Clipboard} from '@angular/cdk/clipboard';
import {MatSnackBar} from '@angular/material/snack-bar';

@Component({
  selector: 'app-thai-calendar',
  templateUrl: './thai-calendar.component.html',
  styleUrls: ['./thai-calendar.component.scss'],
  standalone: true,
  imports: [
    CommonModule,
    MatFormFieldModule,
    MatInputModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatIconModule,
  ],
})
export class ThaiCalendarComponent {
  day: number;
   month: number; 
   year: number;
   displayThai = true;
   
  constructor(
    private readonly clipBoard: Clipboard,
    private readonly snackBar: MatSnackBar,
  ) {
  }

  onDateChangeEvent(event: MatDatepickerInputEvent<Date>) {
    this.day = event.value.getDate()
    this.month = event.value.getMonth()+1
    this.year = event.value.getFullYear()+543

  }

  
}
